.custom_material_dialog {
    .MuiDialogTitle-root .MuiTypography-root, .MuiTypography-body1, .MuiButton-root {
        font: {
            family: "Lato-Bold", "Roboto", "Helvetica", "Arial", sans-serif;
        }
    }
	
	.do-not-show-again-msg .MuiTypography-body1 {
		color: rgba(0, 0, 0, 0.87);
		
		font: {
			family: "Lato-Regular";
			weight: normal;
		}
	}
	
	.MuiDialogTitle-root {
		.dialog-type-icon {
			position: relative;
			top: 2px;
			
			font: {
				size: 20px;
			}
			
			margin: {
				right: 10px;
			}
		}
		
		.dialog-type-icon.warning:before, .dialog-type-icon.confirmation:before {
			color: #F78D24;
			opacity: 1;
		}
		
		.dialog-type-icon.error:before {
			color: #DF0A17;
			opacity: 1;
		}
		
		.dialog-type-icon.success:before {
			color: #4BBC43;
			opacity: 1;
		}
		
		.dialog-type-icon.info:before {
			color: #6AB8F7;
			opacity: 1;
		}
	}
	
	.MuiDialogContent-root {
		.MuiTypography-colorTextSecondary {
			color: rgba(0, 0, 0, 0.87);
		}
	}
}

.custom-snackbar {

	.MuiPaper-elevation6 {
		box-sizing: border-box;
		max-width: 384px;
		align-items: center;
	}

	.MuiAlert-filledWarning {
		background: {
			color: #ED6C03;
		}
	}
	
	.MuiAlert-filledError {
		background: {
			color: #DE350B;
		}
	}
	
	.MuiAlert-filledSuccess {
		background: {
			color: #2E7D32;
		}
	}
	
	.MuiAlert-filledInfo {
		background: {
			color: #0066FF;
		}
	}
	.MuiAlert-icon {
		margin: {
			right: 16px;
		}
	}
	.dg-snackbaricon {
		position: relative;
		top: 1px;
		
		font: {
			size: 20px;
		}
	}
	
	.dg-snackbaricon:before {
		color: #ffffff;
		opacity: 1;
		font: {
			size: 20px !important;
		}
	}
	
	.MuiAlert-message {
		position: relative;
		line-height: 1.5;
  		letter-spacing: 0.15px;
		font: {
			size: 16px;
			weight: normal;
		}
	}
	
	.MuiAlert-action {
		padding: {
			top: 0px;
			left: 5px;
		}

		margin: {
			right: -18px;
		}
	}
	
	.MuiSvgIcon-root {
		font: {
			size: 24px;
		}
	}
}